<template lang="pug">
  .friends-request(v-if="requestFriends.length > 0")
    h4.friends-request__title Заявки в друзья
    ul.friends-request__list
      li.friends-request__item(v-for="user in requestFriends" :key="user.id")
        .friends-request__pic
          img(:src="user.photo" :alt="user.first_name")
        router-link.friends-request__name(:to="{name: 'ProfileId', params: {id: user.id}}") {{user.first_name + ' ' + user.last_name}}
        a.friends-request__link(href="#" @click.prevent="apiAddFriends(user.id)") Добавить
</template>

<script>
  import { mapGetters, mapActions } from 'vuex'
  export default {
    name: 'FriendsRequest',
    computed: {
      ...mapGetters('profile/friends', ['getResultById']),
      requestFriends() {
        return this.getResultById('request')
      }
    },
    methods: {
      ...mapActions('profile/friends', ['apiAddFriends', 'apiRequest'])
    },

    mounted() {
      if (this.requestFriends.length === 0) this.apiRequest()
    }
  }
</script>




